import { useState } from "react";
import { Button, TextField } from '@mui/material';
import Loader from "components/Loader";


export default function SearchBar({ setMovieList, movies }) {
  const [text, setText] = useState("");
  const [loading, setLoading] = useState(false);

  // input  
  const handleText = (e) => {
    setText(e.target.value);
    // input value.length 0    
    if (e.target.value.length === 0) {
      setMovieList(movies);
    }
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    setLoading(true); // laoder 
    // input validate
    if (text.length === 0) return;

    //    
    (async () => {
      const {
        data: { movies },
      } = await (
        await fetch(`https://yts.mx/api/v2/list_movies.json?query_term=${text}`)
      ).json();
      setLoading(false); // laoder 
      setMovieList(movies);
    })();
  };

  return (
    <div>
      { loading ? <Loader /> : null }
      <form onSubmit={handleSubmit} style={{display: 'flex', justifyContent: 'center', alignItems: 'flex-end', gap: '5px'}}>
        <TextField id="standard-basic" label=" " variant="standard"
        value={text}
        onChange={handleText}
        />
        <Button type="submit" size="small" variant="outlined" style={{'color': '#000', 'borderColor': '#000'}}></Button>
      

        {/* <input
          className="search-input"
          value={text}
          onChange={handleText}
          placeholder=""
        /> */}
        {/* <button className="search-btn" type="submit">
          
        </button> */}
      </form>
    </div>
  );
}
